import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import '../node_modules/bootstrap/dist/css/bootstrap.min.css';
import registerServiceWorker from './registerServiceWorker';
import PropTypes from 'prop-types';

var todoItems = [];
todoItems.push({index: 1, value1: "first"});
todoItems.push({index: 2, value1: "second"});
todoItems.push({index: 3, value1: "third"});

class TodoList extends React.Component {
  render () {
    var items = this.props.items.map((item, index) => {
      return (
        <TodoListItem key={index} item={item} index={index} removeItem={this.props.removeItem}  />
      );
    });
    return (
      <ul className="list-group"> {items} </ul>
    );
  }
}

class TodoListItem extends React.Component {
  constructor(props) {
    super(props);
    this.onClickClose = this.onClickClose.bind(this);
    this.handleChange = this.handleChange.bind(this);
  }
  handleChange(updateindex,event) {
  var index = parseInt(updateindex);
  var todo = todoItems[index];
  todoItems.splice(index, 1);
  todo.value1 = event.target.value;
  todoItems.push(todo);
  this.setState({todoItems: todoItems});
  console.log("updated",event.target.value +" On Index : "+updateindex);
}
  onClickClose() {
    var index = parseInt(this.props.index);
    this.props.removeItem(index);
    console.log("Deleted"," On Index : "+index);
  }

  render () {
    var todoClass = this.props.item.done ?
        "done" : "undone";
    return(
      <li className="list-group-item ">
        <div className="undone">
          <input type="text" class="input-style"  defaultValue={this.props.item.value1}  onChange={(e) => this.handleChange(this.props.index, e)}/>
          <button type="button" className="close" onClick={this.onClickClose}>&times;</button>
        </div>
      </li>
    );
  }
}

class TodoForm extends React.Component {
  constructor(props) {
    super(props);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentDidMount() {
    this.refs.itemName.focus();
  }
  onSubmit(event) {
    event.preventDefault();
    var newItemValue = this.refs.itemName.value;

    if(newItemValue) {
      this.props.addItem({newItemValue});
      this.refs.form.reset();
    }
  }
  render () {
    return (
      <form ref="form" onSubmit={this.onSubmit} >
        <input type="text" ref="itemName"  className="form-control" placeholder="add a new todo..."/>
        <br/>
        <button type="submit" className="btn btn-primary btn-block">Save</button>
      </form>
    );
  }
}

class TodoHeader extends React.Component {
  render () {
    return <h1>Todo list</h1>;
  }
}

class TodoApp extends React.Component {
  constructor (props) {
    super(props);
    this.addItem = this.addItem.bind(this);
    this.removeItem = this.removeItem.bind(this);
    this.state = {todoItems: todoItems};
  }
  addItem(todoItem) {
    todoItems.unshift({
      index: todoItems.length+1,
      value1: todoItem.newItemValue,
    });
    this.setState({todoItems: todoItems});
  }
  removeItem (itemIndex) {
    todoItems.splice(itemIndex, 1);
    this.setState({todoItems: todoItems});
  }


  render() {
    return (
      <div id="main">
        <TodoHeader />
        <TodoList items={this.props.initItems} removeItem={this.removeItem} />
        <TodoForm addItem={this.addItem} />
      </div>
    );
  }
}
TodoList.propTypes = {
  todoItems:  PropTypes.oneOfType([
    PropTypes.number,
    PropTypes.string
  ])
};
ReactDOM.render(<TodoApp initItems={todoItems}/>, document.getElementById('root'));
registerServiceWorker();



